<template>
  <v-row>
        <v-col cols="6">
      <v-row>       
        <v-col class="d-flex" cols="4" sm="4">
          <v-select
            :items="numVariables"
            label="Variables"
            v-model="nVariables"
            @change="draw"
            solo
          ></v-select>
        </v-col>
        <v-col class="d-flex" cols="4" sm="4">
          <v-select
            :items="numVariables"
            label="restricciones"
            v-model="nRestricciones"
            @change="draw"
            solo
          ></v-select>
        </v-col>
        <v-col class="d-flex" cols="4" sm="4">
          <v-select
            :items="tipoEjercicio"
            label="accion"
            v-model="ejercicio"
            @change="updateOperation"
            solo
          ></v-select>
        </v-col>      
      </v-row>
      <v-row v-if="variables.length > 0" >
        <v-col cols="1">
          Z =
        </v-col>
        <v-col v-for="(dato, index) of variables" :key="index"  cols="2">
          <InputValue :index="index" :type="1" :label="'X' + (index + 1)" :updateData="update"/>
        </v-col>
        <v-col cols="2">
          Sujeto a:
        </v-col>
      </v-row>
      <v-row v-for="(restriccion, index) of restricciones" :key="index">
        <v-col v-for="(dato, j) of restriccion" :key="j"  cols="2">
          <InputValue :index="index" :subindex="j" :type="4" :label="'X' + (j + 1)" :updateData="updateRestriction"/>
        </v-col>  
        <v-col cols="2">
          <RestrictionType :index="index" :type="2" :updateData="update" />
        </v-col>
        <v-col cols="2">
          <InputValue :label="''" :index="index" :type="3" :updateData="update"/>
        </v-col>
      </v-row>
    </v-col>
    <v-col cols="6">
      <v-row>
        <div class="my-2">
          <v-btn large color="primary" @click="resolver">Resolver</v-btn>
        </div>
      </v-row>
      <v-row>
        <v-alert v-if="status==1" type="info">
          Por Favor envia los datos...
        </v-alert>
        <v-alert v-if="status==2" type="info">
          Calculando...
        </v-alert>
        <v-alert v-if="status==3" type="info">
          <v-list shaped>
              <v-list-item
                v-for="(item, i) in answer"
                :key="i"
              >
                <v-list-item-content>
                  <v-list-item-title v-text="item"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
          </v-list>
        </v-alert>
      </v-row>
    </v-col>
  
  </v-row>
</template>

<script>
import InputValue from "./InputValue";
import RestrictionType from "./RestrictionType";
import axios from "axios";
  export default {
    name: 'Form',
    components: {
      InputValue,
      RestrictionType
    },
    data () {
      return {
        numVariables: ['2', '3', '4'],
        tipoEjercicio: ['maximizar', 'minimizar'],
        ejercicio: 'maximizar',
        nVariables: 0,
        nRestricciones: 0,
        variables: [],
        restricciones: [],
        condiciones: [],
        desigualdades: [],
        maxmin: 'max',
        baseUrl: 'http://www.phpsimplex.com/simplex/page2.php',
        answer: [],
        status: 1
      }
    },
    methods: {
      draw () {
        if (this.nVariables != 0 && this.nRestricciones != 0){
          this.variables = []
          this.restricciones = []
          
          for(let i = 0; i < this.nVariables; i++){
            this.variables[i] = 0
          }

          for(let i = 0; i < this.nRestricciones; i++){
            this.restricciones[i] = []
            this.condiciones[i] = -1
            this.desigualdades[i] = 0
            for(let j = 0; j < this.nVariables; j++) {
              this.restricciones[i][j] = 0
            }
          }
          console.log(this.restricciones)
        }
      },
      updateOperation(){
        this.maxmin = (this.ejercicio == 'maximizar') ? 'max':'min';
      },
      update(type, index, value) { // type: 1 var, 2 condicion, 3 desigualdad
        if (type == 1) this.variables[index] = value
        else if (type == 2) this.condiciones[index] = value
        else if (type == 3) this.desigualdades[index] = value
      },
      updateRestriction(index, subIndex, value) {
        this.restricciones[index][subIndex] = value
      },
      resolver() {
        if (this.nVariables > 0) {
          this.status = 2
          let params = []
          
          params.push(`o=${this.maxmin}`)
          for(let i = 0; i < this.nVariables; i++){
            params.push(`x${i + 1}=${this.variables[i]}`)
          }
          params.push(`rt=${this.nRestricciones}`)
          params.push(`v=${this.nVariables}`)
          params.push(`l=es`)
          for (let i = 0; i < this.nRestricciones; i++) {
            for (let j = 0; j < this.nVariables; j++) {
              params.push(`r${i + 1}_${j + 1}=${this.restricciones[i][j]}`)
            }
            params.push(`d${i + 1}=${this.condiciones[i]}`)
            params.push(`y${i + 1}=${this.desigualdades[i]}`)
          }
          let url = `${this.baseUrl}?${params.join('&')}&Submit=Continuar`;
          console.log(url)
          axios.post('http://34.68.10.101/api/v1/simplex',{
            //"url": "http://www.phpsimplex.com/simplex/page2.php?o=min&x1=1.51&x2=1.68&rt=4&v=2&l=es&r1_1=0.27&r1_2=0.09&d1=1&y1=10000&r2_1=0.41&r2_2=0.27&d2=1&y2=15000&r3_1=0.18&r3_2=0.41&d3=1&y3=20000&r4_1=0.05&r4_2=0.14&d4=1&y4=5000&Submit=Continuar"
            url
          })
            .then(response => {
              
              this.answer = response.data.answer.split('\n')
              this.status = 3
            })
            //.then(json => this.answer[0] = json )
            .catch(err => console.log(err))
          console.log(url)
        }
      }
    },
  }
</script>
