<template>
  <v-text-field :label="label" v-model="value" @change="update"></v-text-field>
</template>

<script>
  export default {
    name: 'Inputvalue',
    props: ['index', 'label', 'subindex', 'updateData', "type"],
    data ( ) {
      return {
        value: 0
      }
    },
    methods: {
      update () {
        if (this.type == 4) {
          console.log('update restriction')
          this.updateData(this.index, this.subindex, 1 * this.value)
        } else {
          console.log('update init...', this.type, this.index, 1 * this.value)
          this.updateData(this.type, this.index, 1 * this.value)
        }
      }
    }
  }
</script>
