<template>
  <v-select
    :items="items"
    v-model="value"
    @change="updateValue"
    solo
  ></v-select>
</template>

<script>
  export default {
    name: 'RestrictionType',
    props: ['index', 'updateData', "type"],
    data () {
      return {
        items: ['<=', '=', '>='],
        value: '<='
      }
    },
    methods: {
      updateValue() {
        let val;
        if (this.value == "<=") val = -1
        if (this.value == "<=") val = 0
        if (this.value == ">=") val = 1
        this.updateData(this.type, this.index, val)
      }
    }
  }
</script>
